
.slider__item {
    max-width: 960px;

    margin: 0 auto;

    background-color: #F5F8FF;
    box-shadow: 0px 15px 100px #e5ebf8;
  }

  .slider__item-inner {
    padding: 20px 15px;

    background-color: #fff;

    @include media(md) {
      padding: 35px 20px;
    }

    @include media(xl) {
      padding: 35px 35px;
    }
  }

  .slider__collage {
    display: grid;
    grid-template-columns: 126px;
    gap: 13px;
    justify-content: center;

    margin-bottom: 30px;

    @include media(sm) {
      display: grid;
      grid-template-columns: 267px 126px;
      grid-auto-rows: minmax(126px, auto);
    }
  }

  .slider__img1 {
    width: 126px;
    height: 126px;

    background-image: url(../images/blog/blog1.png);
    background-size: cover;

    box-shadow: 0px 15px 100px #e5ebf8;

    @include media(sm) {
      grid-area: 1/1/3/2;
      width: 267px;
      height: 267px;
    }
  }

  .slider__img2 {
    width: 126px;
    height: 126px;

    background-image: url(../images/blog/blog2.png);
    background-size: cover;

    box-shadow: 0px 15px 100px #e5ebf8;

    @include media(sm) {
      grid-area: 1/2/2/3;
    }
  }

  .slider__img3 {
    width: 126px;
    height: 126px;

    background-image: url(../images/blog/blog3.png);
    background-size: cover;

    box-shadow: 0px 15px 100px #e5ebf8;

    @include media(lg) {
      grid-area: 2/2/3/3;
    }
  }

  .slider__content {
    padding-left: 30px;

    @include media(lg) {
      text-align: left;
    }
  }

  .slider__title {
    font-family: Quicksand;
    font-size: 36px;
    line-height: 1.2;
    color: #3D4F9F;
  }

  .slider__text {
    margin-bottom: 50px;

    @extend %text;
    font-size: 14px;
  }

  .slider__wrap-btns {
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include media(sm) {
      flex-direction: row;
    }

    @include media(lg) {
      justify-content: left;
    }
  }

  .slider__btn {
    margin-top: 20px;

    @include media(sm) {
      margin-left: 25px;
      margin-top: 0;
    }
  }
